import React from 'react';
import styles from "../Registr/register.module.css";
import videoBG from "../../Assets/IMG/forest.mp4";
import imageCar from "../../Assets/IMG/mercedes.png";

const Registr = () => {

    return (
        <>
            <div className="page">
                <div className={styles.container__video}>
                    <div className={styles.block__video}>
                        <video src={videoBG} autoPlay muted loop/>
                    </div>
                </div>


                <div className={styles.interface}>
                    <h1> !</h1>
                    <h1></h1>
                    <h2>  </h2>
                    <h2>   </h2>
                    <img src={imageCar} alt=""/>
                </div>

                <div className={styles.block_register}>
                    <input className={styles.inputs} type="text" name="FName" placeholder="" autoComplete="sex"/>
                    <input className={styles.inputs} type="text" name="LName" placeholder="" autoComplete="sex"/>
                    <input className={styles.inputs} type="text" name="Login" placeholder=" "  autoComplete="sex"/>
                    <input className={styles.inputs} type="password" name="Password" placeholder=" " />
                    <input className={styles.inputs} type="password" name="Password" placeholder=" "/>
                    <input className={styles.inputs} type="number" name="Phone" placeholder=" " autoComplete="off"/>
                    <span className={styles.block_register_span}>   </span>
                    <input onClick={()=>alert("  ")} type="submit" value=""/>
                </div>
            </div>
        </>
    );
};

export default Registr;